<template lang="pug">
	.words
		canvas(id="my_canvas", width="1920", height="1080")
</template>

<script>
// @ is an alias to /src
import { doc, getFirestore, getDoc, onSnapshot } from "firebase/firestore";
import WordCloud from "wordcloud";

export default {
	name: 'HomeView',
	data (){
		return{
			currentAllUser: 'Nd8gpWnusF4i6cv2fIja',
			messages: [],
			teste: '',
			colors: ['#E5ECFF', '#24BD85', '#FFE5B2', '#D4FFB2', '#FF8E8E'],
			sizes: [4,5,6]
		}
	},
	components: {
	},
	created(){
		this.fetchMessages();
	},
	mounted(){
			
	},
	methods: {
		async renderList(data){
			this.messages = [];

			await data.messages.forEach((val, index) => {
				const random = Math.floor(Math.random() * this.sizes.length);
				let temp = [val.text, this.sizes[random]]
				this.messages.push(temp)
			});

			WordCloud(document.getElementById('my_canvas'), {
				list: this.messages,
				gridSize: Math.round(16 * document.getElementById('my_canvas').width / 1024),
				weightFactor: function (size) {
					return Math.pow(size, 2.3) * document.getElementById('my_canvas').width / 1024;
				},
				fontFamily: 'ff-teko',
				color: (word, weight) => {
					const random = Math.floor(Math.random() * this.colors.length);
					return this.colors[random]
				},
				rotateRatio: 0.5,
				rotationSteps: 2,
				backgroundColor: '#000'
			} );

			// console.log(this.teste)
		},
		async fetchMessages(){
			const db = getFirestore();

			onSnapshot(doc(db, 'users', this.currentAllUser), (doc) => {
				this.renderList(doc.data())
			});
		}
	}
}
</script>
<style lang="stylus" src="@/assets/styles/views/Words.styl"></style>
