<template lang="pug">
	.register
		input(type="text", placeholder="Nome", v-model="user.name")
		input(type="text", placeholder="Email", v-model="user.email")
		input(type="file", accept="image/*", ref="photo", @change="previewImage")

		img(class="preview" :src="preview")
		
		button.btn-send(@click="onUpload", :class="loading ? 'loading' : ''") {{loading ? 'Salvando' : 'Cadastrar'}}

		//- button(@click="creatQrcode") Teste QR

		canvas#canvas(class="qrCode", width="500", height="500")
</template>
<script>
// import firebase from "@/firebase";
// import { getDatabase, ref, set } from "firebase/database";
import { getFirestore, collection, addDoc } from "firebase/firestore";
import { getStorage, ref, uploadBytesResumable, getDownloadURL } from "firebase/storage";

var QRCode = require('qrcode')

export default {
	data() {
		return{
			imageData: '',
			preview: '',
			loading: false,
			user: {
				name: '',
				email: '',
				photo: '',
				messages: []
			}
		}
	},
	name: 'CadastroView',
	components: {
	},
	mounted() {
		this.$root.$el.classList.remove('openMenu')
	},
	methods: {
		async creatQrcode(username){
			let canvas = document.getElementById('canvas')
			
			let opts = {
				type: 'image/jpg',
				scale: 22,
				margin: 1
			}

			QRCode.toCanvas(canvas, 'https://sfpj2022.github.io/#/send/'+username, opts, function (error) {
				if (error) console.error(error)
				console.log('success!');
			})

			canvas.toBlob((blob) => {
				let file = new File([blob], username+'.jpg', { type: "image/jpeg" })

				const storage = getStorage();
				const storageRef = ref(storage, `qrcodes/${file.name}`);
				
				const uploadTask = uploadBytesResumable(storageRef, file);

				uploadTask.on('state_changed', (snapshot) => {
					// Observe state change events such as progress, pause, and resume
					// Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
					const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
					console.log('Upload is ' + progress + '% done');
				},
				(error) => {
					// Handle unsuccessful uploads
				}, () => {
					getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
						console.log('File available at', downloadURL);
					});
				});
				
			}, 'image/jpeg');
		},
		previewImage(event) {
			this.imageData = event.target.files[0];
			this.preview = URL.createObjectURL(this.imageData);
		},
		onUpload(){
			this.loading = true;

			const storage = getStorage();
			const storageRef = ref(storage, `photos/${this.imageData.name}`);
			
			const uploadTask = uploadBytesResumable(storageRef, this.imageData);

			uploadTask.on('state_changed', (snapshot) => {
				// Observe state change events such as progress, pause, and resume
				// Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
				const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
				console.log('Upload is ' + progress + '% done');
			},
			(error) => {
				// Handle unsuccessful uploads
			}, () => {
				getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
					this.user.photo = downloadURL;
					this.submit();
					// console.log('File available at', downloadURL);
				});
			});
		},
		clearInput(){
			this.$refs.photo.value = ''
			this.imageData = ''
			this.preview = ''
			this.user = {
				name: '',
				email: '',
				photo: '',
				messages: []
			}
		},
		async submit() {
			const db = getFirestore();
			let username = this.user.email.match(/^([^@]*)@/)[1];

			try {
				const docRef = await addDoc(collection(db, "users"), {
					username: username.replace(/\./g, ''),
					email: this.user.email,
					name: this.user.name,
					messages: this.user.messages,
					photo: this.user.photo
				});

				await this.creatQrcode(username.replace(/\./g, ''))

				this.loading = false;
				this.clearInput();

				console.log("Document written with ID: ", docRef.id);
			} catch (e) {
				console.error("Error adding document: ", e);
			}
		}
	}
}
</script>
<style lang="stylus" src="@/assets/styles/views/Register.styl"></style>