<template lang="pug">
	#app(:class="{openMenu: isActive}")
		.main-header(@click="openMenu")
			.logo

		.nav(ref="nav")
			.list
				router-link(to='/') Home
				router-link(to='/auth') Minhas mensagens
				router-link(to='/agenda') Programação
				a(class="sair", @click="sair", v-if="onAuth") Sair
				
			.text

		.view(ref='view', @click="closeMenu")
			router-view
</template>

<script>
	import Vue from 'vue'
	import VueCookies from 'vue-cookies-reactive'
	Vue.use(VueCookies)
	
	export default {
		name: 'app',
		data (){
			return{
				isActive: false
			}
		},
		computed:{
			onAuth(){
				return this.$cookies.get('user')
			}
		},
		components: {
		},
		created(){
		},
		methods: {
			sair(){
				this.$cookies.remove('user')
				this.$router.push({name: 'home'})
			},
			openMenu(){
				this.isActive = !this.isActive;
				// this.$root.$el.classList.add('openMenu')
			},
			closeMenu(){
				this.isActive = false;
				// this.$root.$el.classList.remove('openMenu')
			}
		}
	}
</script>

<style lang="stylus" src="@/assets/styles/App.styl"></style>