<template lang="pug">
	.auth
		.header-auth
			img.photo(:src="user.photo")
			span.title {{isAuth ? user.name : 'Minhas mensagens'}}

		.wrap
			.no-auth(v-if="!isAuth")
				input(placeholder="email picpay", v-model="email", autofocus, @keyup="changeInput")
				
				.feedback(v-if="isErr")
					p {{feedback}}

				button.button-submit(
					@click="auth",
					:class="[email.length >= 4 ? '' : 'disable', loading ? 'loading' : '']") {{loading ? 'Entrando' : 'Entrar'}}

			.on-auth(v-else)
				.wrap-input
					textarea-autosize(
						class="input-text",
						placeholder="O que você esta achando do evento...",
						v-model="message",
						@blur.native="blurInput",
						@focus.native="focusInput")

					.buttons
						span.description Envie um elogio, um carinho ou uma piada
						button.button-send(
							@click="send", 
							:class="[loadingSend ? 'loading' : '', message.length >= 5 ? '' : 'disable']")
						
				.list
					span.notfound(v-if="isMessage") Você ainda não tem mensagens
					.message(v-else v-for="message in user.messages", :class="['theme-'+message.configs.theme, 'align-'+message.configs.align, 'font-'+message.configs.font]")
						.borders
							.top
							.bottom

						.text {{message.text}}
</template>

<script>
// @ is an alias to /src
import Vue from 'vue'
import { collection, updateDoc, arrayUnion, query, where, getFirestore, getDocs, doc, getDoc } from "firebase/firestore";
import cookies from 'js-cookie'

import TextareaAutosize from 'vue-textarea-autosize'
Vue.use(TextareaAutosize)

export default {
	name: 'HomeView',
	data (){
		return{
			isErr: false,
			message: '',
			currentAllUser: 'Nd8gpWnusF4i6cv2fIja',
			loadingSend: false,
			loading: false,
			email: '',
			tools: {
				align: 'left',
				font: 'teko',
				theme: 'yellow'
			},
			feedback: 'Não encontramos seu usuário :/',
			isAuth: false,
			user: {}
		}
	},
	computed: {
		isMessage(){
			return this.user.messages && this.user.messages.length ? false : true
		}
	},
	async beforeRouteEnter(to, from, next) {
		if(cookies.get('user')){
			next(vm => {
				vm.fetchAuth(cookies.get('user'))
				vm.isAuth = true;
			});
		}else{
			next(vm => {
				vm.isAuth = false;
			});
		}
	},
	mounted() {
		this.$root.$el.classList.remove('openMenu')
	},
	methods: {
		focusInput(){
			this.$root.$el.classList.add('overlay')
		},
		blurInput(){
			this.$root.$el.classList.remove('overlay')
		},
		async fetchAuth(id){
			const db = getFirestore();
			const snap = await getDoc(doc(db, 'users', id))

			if (snap.exists()) {
				this.user = snap.data();
				this.isAuth = true;
			} else {
				this.isErr = true;
				console.log("No such document")
			}
		},
		setUserDetails(data){
			console.log(data)
		},
		changeInput(){
			console.log('ás')
			this.isErr = false;
		},
		clearData(){
			this.message = ''
		},
		async send(){
			this.loadingSend = true;
			
			const db = getFirestore();
			const user = doc(db, 'users', this.currentAllUser);
			
			await updateDoc(user, {
				messages: arrayUnion({text: this.message, configs: this.tools})
			});

			this.loadingSend = false;
			this.clearData();
		},
		async auth(){
			this.loading = true;
			const db = getFirestore();
			const usersRef = collection(db, 'users');
			// Create a query against the collection.
			const q = query(usersRef, where('email', '==', this.email));
			let querySnapshot = await getDocs(q);

			if(!querySnapshot.empty){
				querySnapshot.forEach((doc) => {
					cookies.set('user', doc.id)
				});
	
				this.fetchAuth(cookies.get('user'));
				this.loading = false;
			}else{
				this.isErr = true;
				this.loading = false;
				console.log('deu não')
			}
		}
	}
}
</script>
<style lang="stylus" src="@/assets/styles/views/Auth.styl"></style>
