<template lang="pug">
	.send
		.frame(:class="'theme-'+tools.theme")
			.border
				.top
				.bottom
				
			.header
				.align(@click="alignText", :class="tools.align")
				.user
					.photo
						img(:src="currenUser.photo")
					span.name {{currenUser.name}}
				.themes(@click="changeThemes")
			.wrap-input(:class="['align-'+tools.align, 'font-'+tools.font]")
				textarea-autosize(
					class="input-text",
					placeholder="Deixe sua mensagem...",
					v-model="message",
					@blur.native="blurInput",
					@focus.native="focusInput")

				.buttons
					span.description Envie um elogio, um carinho ou uma piada
					button.button-send(
						@click="send", 
						:class="[loading ? 'loading' : '', message.length >= 5 ? '' : 'disable']")
			.fonts
				.font(v-for="font in fonts")
					input(type="radio",
						:value="font.font",
						name="font", 
						v-model="tools.font")
					.icon(:class="'font-'+font.font")
						span {{font.text}}
					
</template>
<script>
import Vue from 'vue'
import TextareaAutosize from 'vue-textarea-autosize'
import InputText from '@/components/InputText'
Vue.use(TextareaAutosize)

import { doc, updateDoc, arrayUnion, collection, query, where, getFirestore, getDocs } from "firebase/firestore";
import contenteditable from 'vue-contenteditable'
Vue.use(contenteditable)

export default {
	name: 'SendView',
	data() {
		return{
			fonts: [
				{
					text: 'Aa',
					font: 'teko'
				},
				{
					text: 'Aa',
					font: 'zen'
				},
				{
					text: 'Aa',
					font: 'zilla'
				}
			],
			tools: {
				align: 'left',
				font: 'teko',
				theme: 'yellow'
			},
			loading: false,
			hteste: 0,
			message: '',
			currenUserRef: '',
			currenUser: ''
		}
	},
	components: {
		InputText
	},
	created(){
		this.fetchUser();
	},
	mounted() {
		this.$root.$el.classList.remove('openMenu')
	},
	beforeRouteEnter (to, from, next) {
		if(to.params.username){
			next()
		}else{
			next('/');
		}
	},
	methods: {
		alignText(){
			switch (this.tools.align) {
				case 'left':
					this.tools.align = 'center'
					break;
				case 'center':
					this.tools.align = 'right'
					break;
				default:
					this.tools.align = 'left'
			}
		},
		changeThemes(){
			switch (this.tools.theme) {
				case 'red':
					this.tools.theme = 'blue'
					break;
				case 'blue':
					this.tools.theme = 'green'
					break;
				case 'green':
					this.tools.theme = 'yellow'
					break;
				case 'yellow':
					this.tools.theme = 'greenlight'
					break;
				default:
					this.tools.theme = 'red'
			}
		},
		focusInput(){
			this.$root.$el.classList.add('overlay')
		},
		blurInput(){
			this.$root.$el.classList.remove('overlay')
		},
		onInput(e) {
			this.message = '';
			this.message = e.target.innerText;
		},
		async fetchUser(){
			const db = getFirestore();
			const usersRef = collection(db, 'users');
			// Create a query against the collection.
			const q = query(usersRef, where('username', '==', this.$route.params.username));

			const querySnapshot = await getDocs(q);
			querySnapshot.forEach((doc) => {
				this.currenUserRef = doc.id;
				this.currenUser = doc.data();
			});
		},
		clearData(){
			this.message = '';
		},
		async send(){
			this.loading = true;
			const db = getFirestore();
			const user = doc(db, 'users', this.currenUserRef);
			
			await updateDoc(user, {
				messages: arrayUnion({text: this.message, configs: this.tools})
			});

			this.loading = false;
			this.clearData();
		}
	}
}
</script>
<style lang="stylus" src="@/assets/styles/views/Send.styl"></style>