<template lang="pug">
	.input-text(
		:data-placeholder="placeholder",
		ref="editable",
		contenteditable,
		@click="document.execCommand('bold', false)",
		@focus.stop="focusInput",
		@blur.stop="blurInput",
		v-on="listeners")
					
</template>
<script>
export default {
	name: 'InputText',
	props: {
		placeholder: {
			type: String
		},
		value: {
			type: String,
			default: ''
		}
	},
	data() {
		return{
		}
	},
	computed: {
		listeners() {
			return { ...this.$listeners, input: this.onInput };
		},
	},
	mounted(){
		this.$refs.editable.innerText = this.value;
	},
	components: {
	},
	methods: {
		onInput(e) {
			this.$emit('input', e.target.innerText);
		},
		focusInput(){
			this.$root.$el.classList.add('overlay')
		},
		blurInput(){
			this.$root.$el.classList.remove('overlay')
		}
	}
}
</script>
<style lang="stylus" src="@/assets/styles/components/InputText.styl"></style>