<template lang="pug">
	.users
		.list
			.user(v-for="user in users", @click="goUser(user.username)")
				img.photo(:src="user.photo")
				span.name {{user.name}}
</template>

<script>
// @ is an alias to /src
import { collection, query, where, getFirestore, getDocs } from "firebase/firestore";

export default {
	name: 'HomeView',
	data (){
		return{
			users: []
		}
	},
	components: {
	},
	mounted(){
		this.$root.$el.classList.remove('openMenu')
	},
	created(){
		this.fetchUsers();
	},
	methods: {
		goUser(username){
			this.$router.push({name: 'send', params: {username: username}})
		},
		async fetchUsers(){
			const db = getFirestore();
			const usersRef = collection(db, 'users');

			let querySnapshot = await getDocs(usersRef);
			querySnapshot.forEach((doc) => {
				if (doc.data().username != 'sfpj') {
					this.users.push(doc.data());
				}
			});
		}
	}
}
</script>
<style lang="stylus" src="@/assets/styles/views/Home.styl"></style>
